<template>
	<div id="bckimg"></div>
</template>

<script>
	export default {
		name: 'bckimg',
		mounted: function() {
			var bckimg = document.getElementById("bckimg")
			bckimg.classList.add("bckimgout1")
			var t2 = window.setInterval(function() {
				if(bckimg.getAttribute("class") == 'bckimgout1') {　　
					bckimg.classList.remove("bckimgout1")
					bckimg.classList.add("bckimgout2")
					bckimg.classList.remove("bckimgout3")
					bckimg.classList.remove("bckimgout4")
					bckimg.classList.remove("bckimgout5")
				} else if(bckimg.getAttribute("class") == 'bckimgout2') {
					bckimg.classList.remove("bckimgout1")
					bckimg.classList.add("bckimgout3")
					bckimg.classList.remove("bckimgout2")
					bckimg.classList.remove("bckimgout4")
					bckimg.classList.remove("bckimgout5")
				} else if(bckimg.getAttribute("class") == 'bckimgout3') {
					bckimg.classList.remove("bckimgout1")
					bckimg.classList.add("bckimgout4")
					bckimg.classList.remove("bckimgout2")
					bckimg.classList.remove("bckimgout3")
					bckimg.classList.remove("bckimgout5")
				} else if(bckimg.getAttribute("class") == 'bckimgout4') {
					bckimg.classList.remove("bckimgout1")
					bckimg.classList.add("bckimgout5")
					bckimg.classList.remove("bckimgout2")
					bckimg.classList.remove("bckimgout3")
					bckimg.classList.remove("bckimgout4")
				} else if(bckimg.getAttribute("class") == 'bckimgout5') {
					bckimg.classList.remove("bckimgout5")
					bckimg.classList.add("bckimgout1")
					bckimg.classList.remove("bckimgout2")
					bckimg.classList.remove("bckimgout3")
					bckimg.classList.remove("bckimgout4")
				}
			}, 5000)
		}
	}
</script>

