				//反向点亮导航
				for(var i = 0; i < gopage.length; i++) {
					if(gopage[i].offsetTop - document.documentElement.scrollTop <= clientHeight / 2 && gopage[i].offsetTop - document.documentElement.scrollTop > -clientHeight / 2) {
						navname[i].classList.add("active")
					} else {
						navname[i].classList.remove("active")
					}
				}

